import React from 'react';
import { Link } from "react-router-dom";
import '../styles/section.css';

const Section = (props) => {
	let chapters = props.chapters.map((chapter, i) => {     
		return (
			<Link key={i} to={"/sections/"+props.section+"/"+chapter.link} className="chapter clear">
				<div className="name">{chapter.name}</div>
				<div className="arrow"></div>
			</Link>
		)                 
	})

    return(
        <div className="section">
    		<p>{props.name}</p>
    		<div className="chapters shadow">
    			{chapters}
    		</div>
    	</div>
    )
}

export default Section;